.container{
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies{
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr)); 
  /* 2개열이 같은 */
  grid-gap: 100px;
  padding: 50px;
  width: 80%;
  padding-top: 70px;
  background-color: yellow;
  
}

@media screen and (max-width: 767px) {
  /* 0 ~ 767 */
 .movies{
  grid-template-columns: 1fr;
  width: 100%;
 } 
}